.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: $black-pearl;
  background-image: url(../img/fonheader.png);
  background-repeat: no-repeat;
  z-index: 100;

  @media (max-width: 1023px) {
    position: relative;
  }
}

.header__wrapper {
  padding-top: 30px;
  padding-bottom: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 767px) {
    padding-bottom: 20px;
  }
}

