@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}
a:hover {
  opacity: 0.5;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 0;
}
@media (max-width: 1024px) {
  h1 {
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 768px) {
  h1 {
    font-size: 36px;
    line-height: 42px;
  }
}

h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 0;
}
@media (max-width: 768px) {
  h2 {
    font-size: 28px;
    line-height: 40px;
  }
}

h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 33px;
  font-weight: 400;
}
@media (max-width: 768px) {
  h3 {
    font-size: 20px;
    line-height: 28px;
  }
}

h4 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 410px 0 350px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 110px;
  }
}
@media (max-width: 1279px) {
  .container {
    padding: 0 75px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 15px 10px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background-color: #041827;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--border {
  border: 1px solid #ffffff;
}
.btn--small {
  font-size: 14px;
  color: #00B2FF;
  background-color: #ffffff;
  border: 1px solid #00B2FF;
  padding: 12px 14px;
}
.btn--large {
  font-size: 18px;
  background-color: #00B2FF;
  padding: 25px 15px;
}
@media (max-width: 768px) {
  .btn--large {
    font-size: 14px;
    padding: 10px 30px;
  }
}
.btn--black {
  background-color: #041827;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .btn--none {
    display: none;
  }
}

.btn__text {
  display: block;
}
.btn__text--mobile {
  display: none;
}
@media (max-width: 768px) {
  .btn__text {
    display: none;
  }
  .btn__text--mobile {
    display: block;
  }
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #041827;
  background-image: url(../img/fonheader.png);
  background-repeat: no-repeat;
  z-index: 100;
}
@media (max-width: 1023px) {
  .header {
    position: relative;
  }
}

.header__wrapper {
  padding-top: 30px;
  padding-bottom: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .header__wrapper {
    padding-bottom: 20px;
  }
}

